const actions = {
  getJobCostingReports: (page, search, filter, filter_select) => ({
    type: "GET_JOB_COSTING_REPORTS_REQUEST",
    payload: {
      page: page,
      search: search,
      filter: filter,
      filter_select
    }
  }),
  clearSuccess: () => ({
    type: "CLEAR_SUCCESS"
  }),
  clearError: () => ({
    type: "CLEAR_ERROR"
  }),
  clearErrorField: () => ({
    type: "CLEAR_ERROR_FIELD"
  })
};

export default actions;
