import React, { Component } from "react";
import { connect } from "react-redux";
import { compose } from "redux";
import moment from "moment";

import {
  ExcelExport,
  ExcelExportColumn,
  ExcelExportColumnGroup
} from "@progress/kendo-react-excel-export";
import { getFileName } from "../../helpers/helper";

import ButtonExcel from "./ButtonExcel";
import getLanguage from "./MyLanguange";

/**
 *
 * @param {string} color warna border
 */
const bordered = color => {
  if (color) {
    return {
      borderTop: { color: color },
      borderRight: { color: color },
      borderBottom: { color: color },
      borderLeft: { color: color }
    };
  } else {
    return {
      borderTop: { color: "#000000" },
      borderRight: { color: "#000000" },
      borderBottom: { color: "#000000" },
      borderLeft: { color: "#000000" }
    };
  }
};

/**
 *
 * @param {boolean} bold bold atau tidak
 * @param {string} background warna cell
 * @param {number} fontSize warna cell
 */
const headerTitle = (bold, background, fontSize) => ({
  textAlign: "center",
  bold: bold,
  background: background,
  color: "#303030",
  fontSize: fontSize
});

const headerCell = {
  textAlign: "center",
  verticalAlign: "center"
};

/**
 * @param {array} locations list location yg ada
 * @param {string} id_location {OPTIONAL} id_location yang ingin dicari
 */
const getLocation = (locations, id_location) => {
  let location_name = getLanguage({ id: "All" });
  const str = getLanguage({ id: "sidebar.location" });

  if (id_location) {
    locations.map(data =>
      data.id_location === id_location
        ? (location_name = data.location_name)
        : null
    );
  }

  return `${str} : ${location_name}`;
};

const getDepartment = (departments, id_department) => {
  let department_name = getLanguage({ id: "All" });
  const str = getLanguage({ id: "sidebar.department" });

  if (id_department) {
    departments.map(data =>
      data.id_department === id_department
        ? (department_name = data.department_name)
        : null
    );
  }

  return `${str} : ${department_name}`;
};

const getProject = (projects, id_project) => {
  let project_name = getLanguage({ id: "All" });
  const str = getLanguage({ id: "sidebar.project" });

  if (id_project) {
    projects.map(data =>
      data.id_project === id_project ? (project_name = data.project_name) : null
    );
  }

  return `${str} : ${project_name}`;
};

const getCashBank = (cash_banks, id_cash_bank) => {
  let cash_bank_name = getLanguage({ id: "All" });
  const str = getLanguage({ id: "sidebar.cashBank" });

  if (id_cash_bank) {
    cash_banks.map(data =>
      data.id_cash_bank === id_cash_bank
        ? (cash_bank_name =
            data.bank_account_number === null
              ? data.cash_bank_code
              : data.cash_bank_code + " (" + data.bank_account_number + ")")
        : null
    );
  }

  return `${str} : ${cash_bank_name}`;
};

class ExcelWrapper extends Component {
  state = {
    data: [],
    raw_data: []
  };

  _exporter;
  export = () => {
    this.save(this._exporter);
  };

  getCells = row => {
    let newCells = [];
    row.cells.map(data => {
      if (data.spanValue !== 0) {
        data.colSpan = data.spanValue;
        data.background = "#e5e3e3";
        data.bold = true;
        data = { ...data, ...bordered() };
        if (!data.parse) {
          data.textAlign = "center";
        }
        newCells.push(data);
      }
    });

    return newCells;
  };

  save = component => {
    const { data } = this.props;

    const options = component.workbookOptions();
    let rows = options.sheets[0].rows;
    let header_key = 0;

    // console.log(options);
    rows.map((row, key) => {
      if (row.type === "data") {
        row.cells.map(cell => {
          // untuk parse value
          if (cell.parse) {
            const value =
              cell.value || cell.value === 0 ? parseFloat(cell.value) : null;
            cell.value = value;
          }

          // untuk format date dari API
          if (cell.date) {
            const dateFormat = cell.dateFormat
              ? cell.dateFormat
              : "DD MMMM YYYY HH:mm:ss";
            const value = cell.value;
            cell.value = value ? moment(value).format(dateFormat) : null;
          }

          // untuk colspan menggunakan check value
          if (cell.spanCheck && cell.spanCheck === "string") {
            if (cell.value === cell.spanCheckValue) {
              row.cells = this.getCells(row);
            }
          }

          // untuk colspan menggunakan check isNan
          if (cell.spanCheck && cell.spanCheck === "isNan") {
            const raw_row = data[key - header_key - 1];
            if (isNaN(raw_row[cell.spanCheckValue])) {
              cell.colSpan = cell.spanValue;
            }
          }
        });
      } else {
        header_key = key;
      }
    });

    component.save(options);
  };

  checkProps = () => {
    const { filter, locations, departments, projects, cash_banks } = this.props;

    if (
      filter &&
      locations &&
      (departments ||
        projects ||
        cash_banks ||
        (filter.stock_age && filter.bank_interest) ||
        (filter.operator && filter.current_balance) ||
        (filter.first_transaction_date && filter.second_transaction_date) ||
        (filter.first_start_date && filter.second_start_date) ||
        ((filter.ap_aging || filter.ar_aging) &&
          filter.fromDays &&
          filter.toDays))
    ) {
      return true;
    } else return false;
  };

  formatHeader = () => {
    const {
      filter,
      locations,
      departments,
      projects,
      cash_banks,
      filterLocation,
      filterDepartment,
      filterProject,
      filterCashBank
    } = this.props;

    let str = `${getLocation(
      locations,
      filterLocation ? filter[filterLocation] : filter.id_location
    )}, `;
    if (departments)
      str += `${getDepartment(
        departments,
        filterDepartment ? filter[filterDepartment] : filter.id_department
      )}`;
    if (projects)
      str += `${getProject(
        projects,
        filterProject ? filter[filterProject] : filter.id_project
      )}`;
    if (cash_banks)
      str += `${getCashBank(
        cash_banks,
        filterCashBank ? filter[filterCashBank] : filter.id_cash_bank
      )}`;
    if (filter.first_transaction_date)
      str += `${getLanguage({ id: "previousDate" })} : ${moment(
        filter.first_transaction_date
      ).format("DD MMMM YYYY HH:mm:ss")}, `;
    if (filter.second_transaction_date)
      str += `${getLanguage({ id: "currentDate" })} : ${moment(
        filter.second_transaction_date
      ).format("DD MMMM YYYY HH:mm:ss")}`;
    if (filter.first_start_date)
      str += `${getLanguage({ id: "previousDate" })} : ${moment(
        filter.first_start_date
      ).format("DD MMMM YYYY HH:mm:ss")} - ${moment(
        filter.first_end_date
      ).format("DD MMMM YYYY HH:mm:ss")}, `;
    if (filter.second_start_date)
      str += `${getLanguage({ id: "currentDate" })} : ${moment(
        filter.second_start_date
      ).format("DD MMMM YYYY HH:mm:ss")} - ${moment(
        filter.second_end_date
      ).format("DD MMMM YYYY HH:mm:ss")}, `;
    if (filter.stock_age && filter.bank_interest)
      str += `${getLanguage({ id: "stockAgeBankInterest" })} : ${
        filter.stock_age
      } (${getLanguage({
        id: "locations.add.leadTime.unit"
      })})/${filter.bank_interest.toFixed(2)}%`;
    if (filter.operator && filter.current_balance)
      str += `${getLanguage({ id: "currentBalance" })} ${
        filter.operator
      } ${filter.current_balance.toFixed(2)}`;
    if (
      (filter.ap_aging || filter.ar_aging) &&
      filter.fromDays &&
      filter.toDays
    )
      str += `${getLanguage({
        id: "agingLengthParam",
        values: {
          fromDays: filter.fromDays,
          toDays: filter.toDays,
          agingParam:
            filter.ap_aging === "from_transaction_date" ||
            filter.ar_aging === "from_transaction_date"
              ? getLanguage({
                  id: "accPayAgingReports.filter.transDate"
                })
              : getLanguage({
                  id: "accPayAgingReports.filter.dueDate"
                })
        }
      })}`;

    return str;
  };

  render() {
    const {
      user,
      title,
      fileName,
      data,
      columns,
      loading,
      filter,
      locations,
      filterLocation,
      filterDate,
      dateFormat
    } = this.props;

    // console.log("filter dari excel wrapper", filter);

    let { company } = user ? user : [];
    company = company ? company : [];

    let report_period;

    const from =
      filter && filter.start_date
        ? filter.start_date instanceof moment
          ? filter.start_date
          : moment(filter.start_date, "YYYY-MM-DD HH:mm:ss")
        : null;
    const to =
      filter && filter.end_date
        ? filter.end_date instanceof moment
          ? filter.end_date
          : moment(filter.end_date, "YYYY-MM-DD HH:mm:ss")
        : null;
    const on =
      filter && filter[filterDate]
        ? filter[filterDate] instanceof moment
          ? filter[filterDate].format(`${dateFormat}`)
          : moment(filter[filterDate], "YYYY-MM-DD HH:mm:ss").format(
              `${dateFormat}`
            )
        : null;

    if (from && to) {
      // report_period = `From ${from.format("DD MMMM YYYY")} to ${to.format(
      //   "DD MMMM YYYY"
      // )}`;
      report_period = getLanguage({
        id: "reportDateRange",
        values: {
          fromDate: from.format("DD MMMM YYYY"),
          toDate: to.format("DD MMMM YYYY")
        }
      });
    } else if (from) {
      // report_period = `From ${from.format("DD MMMM YYYY")}`;
      report_period = getLanguage({
        id: "reportDate",
        values: {
          fromDate: from.format("DD MMMM YYYY")
        }
      });
    } else if (on) {
      report_period = on;
    }

    const content = columns.map((data, key) =>
      data.children ? (
        <ExcelExportColumnGroup
          key={key}
          title={data.title}
          headerCellOptions={{ ...headerCell, ...bordered() }}
        >
          {data.children.map((child, index) => (
            <ExcelExportColumn
              key={key + index}
              field={child.dataIndex}
              title={child.title}
              cellOptions={{
                format: child.parse ? "#,##0.##;-#,##0.##" : "General",
                parse: child.parse,
                date: child.date,
                dateFormat: child.dateFormat,
                spanCheck: child.spanCheck,
                spanCheckValue: child.spanCheckValue,
                spanValue: child.spanValue
              }}
              headerCellOptions={{
                ...headerCell,
                ...bordered()
              }}
            />
          ))}
        </ExcelExportColumnGroup>
      ) : (
        <ExcelExportColumn
          key={key}
          field={data.dataIndex}
          title={data.title}
          cellOptions={{
            format: data.parse ? "#,##0.##;-#,##0.##" : "General",
            parse: data.parse,
            date: data.date,
            dateFormat: data.dateFormat,
            spanCheck: data.spanCheck,
            spanCheckValue: data.spanCheckValue,
            spanValue: data.spanValue
          }}
          headerCellOptions={{
            ...headerCell,
            ...bordered()
          }}
        />
      )
    );

    return (
      <>
        <ButtonExcel
          onClick={this.export}
          loading={loading}
          style={{ marginLeft: "5px" }}
        />

        <ExcelExport
          data={data}
          fileName={getFileName(fileName, null, false) + ".xlsx"}
          ref={exporter => {
            this._exporter = exporter;
          }}
        >
          {/*  Header */}
          <ExcelExportColumnGroup
            title={company.company_name}
            headerCellOptions={headerTitle(true, "", 16)}
          >
            <ExcelExportColumnGroup
              title={getLanguage({ id: title })}
              headerCellOptions={headerTitle(true, "", 16)}
            >
              {this.checkProps() ? (
                <ExcelExportColumnGroup
                  title={report_period ? report_period : this.formatHeader()}
                  headerCellOptions={headerTitle(false, "", 12)}
                >
                  <ExcelExportColumnGroup
                    title={report_period ? this.formatHeader() : null}
                    headerCellOptions={headerTitle(false, "", 12)}
                  >
                    <ExcelExportColumnGroup
                      headerCellOptions={headerTitle(false, "", 12)}
                    >
                      {content}
                    </ExcelExportColumnGroup>
                  </ExcelExportColumnGroup>
                </ExcelExportColumnGroup>
              ) : (
                <ExcelExportColumnGroup
                  title={
                    report_period
                      ? report_period
                      : locations && filter
                      ? getLocation(
                          locations,
                          filterLocation
                            ? filter[filterLocation]
                            : filter.id_location
                        )
                      : null
                  }
                  headerCellOptions={headerTitle(false, "", 12)}
                >
                  {locations && filter && report_period ? (
                    <ExcelExportColumnGroup
                      title={getLocation(
                        locations,
                        filterLocation
                          ? filter[filterLocation]
                          : filter.id_location
                      )}
                      headerCellOptions={headerTitle(false, "", 12)}
                    >
                      {content}
                    </ExcelExportColumnGroup>
                  ) : (
                    content
                  )}
                </ExcelExportColumnGroup>
              )}
            </ExcelExportColumnGroup>
          </ExcelExportColumnGroup>
        </ExcelExport>
      </>
    );
  }
}

const mapStateToProps = state => {
  return { user: state.auth.user };
};

export default compose(connect(mapStateToProps))(ExcelWrapper);
