import React, { Component } from "react";
import { connect } from "react-redux";
import { compose } from "redux";

import moment from "moment";
import IntlMessages from "../../components/utility/intlMessages";
import { PDFExport } from "@progress/kendo-react-pdf";
import { Col, Divider, Row } from "antd";

import { Text } from "./app.style";
import { getFileName } from "../../helpers/helper";
import { siteConfig } from "../../settings";
import getLanguage from "./MyLanguange";
import PageTemplate from "./pageTemplate";
import PrintTable from "../Tables/antTables/PrintTable.style";
import ButtonPDF from "./ButtonPDF";
import "./pdf.css";

// function untuk get location name
const getLocation = (locations, id_location) => {
  let location_name = <IntlMessages id="All" />;

  if (id_location) {
    locations.map(data =>
      data.id_location === id_location
        ? (location_name = data.location_name)
        : null
    );
  }

  return location_name;
};

// function untuk get department name
const getDepartment = (departments, id_department) => {
  let department_name = <IntlMessages id="All" />;

  if (id_department) {
    departments.map(data =>
      data.id_department === id_department
        ? (department_name = data.department_name)
        : null
    );
  }

  return department_name;
};

// function untuk get project name
const getProject = (projects, id_project) => {
  let project_name = <IntlMessages id="All" />;

  if (id_project) {
    projects.map(data =>
      data.id_project === id_project ? (project_name = data.project_name) : null
    );
  }

  return project_name;
};

// function untuk get cash bank name
const getCashBank = (cash_banks, id_cash_bank) => {
  let cash_bank_name = <IntlMessages id="All" />;

  if (id_cash_bank) {
    cash_banks.map(data =>
      data.id_cash_bank === id_cash_bank
        ? (cash_bank_name =
            data.bank_account_number === null
              ? data.cash_bank_code
              : data.cash_bank_code + " (" + data.bank_account_number + ")")
        : null
    );
  }

  return cash_bank_name;
};

// function untuk get trans type name
const getTransTypeString = data => {
  let transaction_type_string;

  switch (data) {
    case 1:
      transaction_type_string = getLanguage({ id: "openingBalance" });
      break;
    case 2:
      transaction_type_string = getLanguage({ id: "sidebar.issueReceipt" });
      break;
    case 3:
      transaction_type_string = getLanguage({ id: "sidebar.issueReceipt" });
      break;
    case 4:
      transaction_type_string = getLanguage({ id: "sidebar.purchaseReceipts" });
      break;
    case 5:
      transaction_type_string = getLanguage({ id: "sidebar.purchaseInvoices" });
      break;
    case 6:
      transaction_type_string = getLanguage({ id: "sidebar.purchaseReturns" });
      break;
    case 7:
      transaction_type_string = getLanguage({ id: "sidebar.itemTransfer" });
      break;
    case 8:
      transaction_type_string = getLanguage({ id: "sidebar.deliveryOrder" });
      break;
    case 9:
      transaction_type_string = getLanguage({ id: "sidebar.salesInvoice" });
      break;
    case 11:
      transaction_type_string = getLanguage({ id: "sidebar.salesReturn" });
      break;
    case 12:
      transaction_type_string = getLanguage({ id: "sidebar.fixedAsset" });
      break;
    case 13:
      transaction_type_string = getLanguage({ id: "journalVoucher" });
      break;
    case 14:
      transaction_type_string = getLanguage({ id: "accountReceivablePayment" });
      break;
    case 15:
      transaction_type_string = getLanguage({ id: "accountPayablePayment" });
      break;
    case 16:
      transaction_type_string = getLanguage({ id: "creditMemo" });
      break;
    case 17:
      transaction_type_string = getLanguage({ id: "debitMemo" });
      break;
    case 18:
      transaction_type_string = getLanguage({ id: "sidebar.cashManagement" });
      break;
    case 19:
      transaction_type_string = getLanguage({ id: "sidebar.jobCosting" });
      break;
    case 20:
      transaction_type_string = getLanguage({ id: "sidebar.jobCostRollover" });
      break;
    case 21:
      transaction_type_string = getLanguage({ id: "supplierDeposit" });
      break;
    case 22:
      transaction_type_string = getLanguage({ id: "customerDeposit" });
      break;
    default:
      transaction_type_string = "-";
  }
  return transaction_type_string;
};

class PDFWrapper extends Component {
  pdfExportComponent;

  state = {};

  exportPDF = () => {
    this.pdfExportComponent.save();
  };

  render() {
    const {
      cash_banks, //array of object - list cash banks dari foreign keys - wajib apabila perlu export cash/bank name
      columns, //array of object - kolom dari tabel
      data, //array of object - datasource dari tabel
      departments, //array of object - list departments dari foreign keys - wajib apabila perlu export department name
      defaultExpandAllRows, //boolean - untuk tabel yang setiap row nya memiliki sub table
      expandedRowKeys, //array - key dari expandedRow
      expandedRowRender, //array of object - element yang akan dirender pada row yang memiliki sub table
      fileName, //string - nama file .pdf yang akan diexport
      filter, //string or object filter dari report
      keepTogether, //string - menghindari split element pada page break
      landscape, //boolean - properti untuk landscape orientation
      loading, //boolean - loading untuk button
      locations, //array of object - list locations dari foreign keys - wajib apabila perlu export location name
      margin, //string - setting margin untuk kertas
      projects, //array of object - list projects dari foreign keys
      scale, //number - pengaturan scale halaman
      separateTable, //boolean - pengaturan untuk laporan dengan rendering table-tablenya terpisah
      summariesColumns, //array of object - kolom dari tabel summary
      summariesData, //array of object - dataSource dan tabel summary
      title, //string - judul dokument .pdf
      user
    } = this.props;

    let { company } = user ? user : [];
    company = company ? company : [];
    return (
      <>
        <ButtonPDF
          loading={loading}
          onClick={this.exportPDF}
          style={{ marginLeft: "5px" }}
        ></ButtonPDF>
        <div style={{ position: "fixed", left: "10000px" }}>
          <PDFExport
            author={siteConfig.footerReport}
            date={new Date(moment().format("DD MMMM YYYY - HH:mm:ss"))}
            fileName={getFileName(fileName, null, false) + ".pdf"}
            forceToBreak="ant-table-row"
            keepTogether={keepTogether ? keepTogether : ""}
            landscape={landscape ? landscape : false}
            margin={margin ? margin : "1.5cm"}
            paperSize="A4"
            pageTemplate={PageTemplate}
            ref={component => (this.pdfExportComponent = component)}
            scale={scale ? scale : 0.8}
          >
            <div className="pdf-header">
              <Text>{company.company_name}</Text>
              <h3>{<IntlMessages id={title}></IntlMessages>}</h3>
              {/* filter untuk start date dan end date */}
              {filter && filter.start_date && filter.end_date ? (
                <div>
                  <IntlMessages
                    id="reportDateRange"
                    values={{
                      fromDate: moment(filter.start_date).format(
                        "DD MMMM YYYY"
                      ),
                      toDate: moment(filter.end_date).format("DD MMMM YYYY")
                    }}
                  />
                </div>
              ) : null}
              {/* filter untuk aging report */}
              {filter &&
              (filter.ap_aging || filter.ar_aging) &&
              filter.fromDays &&
              filter.toDays ? (
                <div>
                  <IntlMessages
                    id="agingLength"
                    values={{
                      fromDays: filter.fromDays,
                      toDays: filter.toDays,
                      agingParam:
                        filter.ap_aging === "from_transaction_date" ||
                        filter.ar_aging === "from_transaction_date"
                          ? getLanguage({
                              id: "accPayAgingReports.filter.transDate"
                            })
                          : getLanguage({
                              id: "accPayAgingReports.filter.dueDate"
                            })
                    }}
                  />
                </div>
              ) : null}
            </div>

            {filter ? (
              <div className="filter">
                {/* <h5>Filter Data</h5> */}

                {/* filter untuk location */}
                {filter.id_location && locations ? (
                  <Row>
                    <Col span={5}>
                      <IntlMessages id="sidebar.location"></IntlMessages>
                    </Col>
                    <Col span={1}>:</Col>
                    <Col span={15}>
                      {getLocation(locations, filter.id_location)}
                    </Col>
                  </Row>
                ) : locations ? (
                  <Row>
                    <Col span={5}>
                      <IntlMessages id="sidebar.location"></IntlMessages>
                    </Col>
                    <Col span={1}>:</Col>
                    <Col span={15}>
                      <IntlMessages id="allLocations"></IntlMessages>
                    </Col>
                  </Row>
                ) : null}

                {/* untuk filter previous date */}
                {filter.first_transaction_date ? (
                  <Row>
                    <Col span={5}>
                      <IntlMessages id="previousDate"></IntlMessages>
                    </Col>
                    <Col span={1}>:</Col>
                    <Col span={15}>
                      {moment(filter.first_transaction_date).format(
                        "DD MMMM YYYY HH:mm:ss"
                      )}
                    </Col>
                  </Row>
                ) : null}

                {/* untuk filter current date */}
                {filter.second_transaction_date ? (
                  <Row>
                    <Col span={5}>
                      <IntlMessages id="currentDate"></IntlMessages>
                    </Col>
                    <Col span={1}>:</Col>
                    <Col span={15}>
                      {moment(filter.second_transaction_date).format(
                        "DD MMMM YYYY HH:mm:ss"
                      )}
                    </Col>
                  </Row>
                ) : null}

                {/* untuk filter previous date range */}
                {filter.first_start_date ? (
                  <Row>
                    <Col span={5}>
                      <IntlMessages id="previousDate"></IntlMessages>
                    </Col>
                    <Col span={1}>:</Col>
                    <Col span={15}>
                      {moment(filter.first_start_date).format(
                        "DD MMMM YYYY HH:mm:ss"
                      ) +
                        " - " +
                        moment(filter.first_end_date).format(
                          "DD MMMM YYYY HH:mm:ss"
                        )}
                    </Col>
                  </Row>
                ) : null}

                {/* untuk filter previous date range */}
                {filter.second_start_date ? (
                  <Row>
                    <Col span={5}>
                      <IntlMessages id="currentDate"></IntlMessages>
                    </Col>
                    <Col span={1}>:</Col>
                    <Col span={15}>
                      {moment(filter.second_start_date).format(
                        "DD MMMM YYYY HH:mm:ss"
                      ) +
                        " - " +
                        moment(filter.second_end_date).format(
                          "DD MMMM YYYY HH:mm:ss"
                        )}
                    </Col>
                  </Row>
                ) : null}

                {/* untuk filter trans_year */}
                {filter.trans_year ? (
                  <Row>
                    <Col span={5}>
                      <IntlMessages id="retainedEarningReports.filter.transYear"></IntlMessages>
                    </Col>
                    <Col span={1}>:</Col>
                    <Col span={15}>{filter.trans_year}</Col>
                  </Row>
                ) : null}

                {/* untuk filter month_year */}
                {filter.month_year ? (
                  <Row>
                    <Col span={5}>
                      <IntlMessages id="monthYear"></IntlMessages>
                    </Col>
                    <Col span={1}>:</Col>
                    <Col span={15}>{filter.month_year.format("MMMM YYYY")}</Col>
                  </Row>
                ) : null}

                {/* untuk filter month_year */}
                {filter.year ? (
                  <Row>
                    <Col span={5}>
                      <IntlMessages id="year"></IntlMessages>
                    </Col>
                    <Col span={1}>:</Col>
                    <Col span={15}>{filter.year.format("YYYY")}</Col>
                  </Row>
                ) : null}

                {/* filter untuk department */}
                {filter.id_department && departments ? (
                  <Row>
                    <Col span={5}>
                      <IntlMessages id="sidebar.department"></IntlMessages>
                    </Col>
                    <Col span={1}>:</Col>
                    <Col span={15}>
                      {getDepartment(departments, filter.id_department)}
                    </Col>
                  </Row>
                ) : null}

                {/* filter untuk project */}
                {filter.id_project && projects ? (
                  <Row>
                    <Col span={5}>
                      <IntlMessages id="sidebar.project"></IntlMessages>
                    </Col>
                    <Col span={1}>:</Col>
                    <Col span={15}>
                      {getProject(projects, filter.id_project)}
                    </Col>
                  </Row>
                ) : null}

                {/* filter untuk cash_bank */}
                {filter.id_cash_bank && cash_banks ? (
                  <Row>
                    <Col span={5}>
                      <IntlMessages id="sidebar.cashBank"></IntlMessages>
                    </Col>
                    <Col span={1}>:</Col>
                    <Col span={15}>
                      {getCashBank(cash_banks, filter.id_cash_bank)}
                    </Col>
                  </Row>
                ) : null}

                {/* filter untuk transaction date */}
                {filter.transaction_date ? (
                  <Row>
                    <Col span={5}>
                      <IntlMessages id="asOf"></IntlMessages>
                    </Col>
                    <Col span={1}>:</Col>
                    <Col span={15}>
                      {moment(filter.transaction_date).format(
                        "DD MMMM YYYY HH:mm:ss"
                      )}
                    </Col>
                  </Row>
                ) : null}

                {filter.stock_age && filter.bank_interest ? (
                  <div style={{ textAlign: "left" }}>
                    <Row>
                      <Col span={5}>
                        <IntlMessages id="stockAgeBankInterest"></IntlMessages>
                      </Col>
                      <Col span={1}>:</Col>
                      <Col span={15}>
                        <IntlMessages id="locations.add.leadTime.unit">
                          {txt =>
                            `${
                              filter.stock_age
                            } (${txt})/${filter.bank_interest.toFixed(2)}%`
                          }
                        </IntlMessages>
                      </Col>
                    </Row>
                  </div>
                ) : null}
              </div>
            ) : null}

            {/* summary table */}
            {summariesColumns ? (
              <>
                <Divider
                  orientation="left"
                  style={{
                    fontSize: "12px",
                    alignItems: "center",
                    display: "flex"
                  }}
                >
                  <IntlMessages id="summaries" style={{ marginTop: "-10" }} />
                </Divider>

                <PrintTable
                  columns={summariesColumns}
                  dataSource={summariesData}
                  pagination={false}
                />

                <Divider
                  orientation="left"
                  style={{
                    fontSize: "12px",
                    alignItems: "center",
                    display: "flex"
                  }}
                >
                  <IntlMessages id="details" style={{ marginTop: "-10" }} />
                </Divider>
              </>
            ) : null}
            {/* end of summary table */}

            {/* content report dalam bentuk table */}

            {separateTable ? (
              data.map((item, index) => {
                if (item[1]) {
                  item[1][0].transaction_type_string = getTransTypeString(
                    item[1][0].transaction_type
                  );
                  item.dataSource = [];
                  var totalCredit = [];
                  var totalDebit = [];
                  item[1].map(x => {
                    if (x.debit_credit === 1) {
                      totalDebit.push(x.amount);
                      return item.dataSource.push({
                        ...x,
                        key: x.id_gl_transaction,
                        debit: x.amount,
                        credit: null
                      });
                    } else {
                      totalCredit.push(x.amount);
                      return item.dataSource.push({
                        ...x,
                        key: x.id_gl_transaction,
                        debit: null,
                        credit: x.amount
                      });
                    }
                  });
                  var debit = "";
                  var credit = "";
                  if (totalCredit.length > 0) {
                    credit = totalCredit.reduce(function(a, b) {
                      return parseFloat(a) + parseFloat(b);
                    });
                  }
                  if (totalDebit.length > 0) {
                    debit = totalDebit.reduce(function(a, b) {
                      return parseFloat(a) + parseFloat(b);
                    });
                  }
                  item.dataSource.push({
                    key:
                      item[1][0].id_gl_transaction +
                      item[1][0].id_source_transaction,
                    account_code: "Total",
                    debit: parseFloat(debit),
                    credit: parseFloat(credit)
                  });
                }
                // console.log("data parent")
                return (
                  <div key={index}>
                    <Row
                      style={{
                        fontSize: "12px",
                        marginBottom: "5px",
                        textAlign: "left"
                      }}
                    >
                      <Col span={4}>
                        <strong>
                          <IntlMessages id="salesReturns.filter.date"></IntlMessages>
                        </strong>
                      </Col>
                      <Col span={1}>:</Col>
                      <Col span={19}>
                        {moment(item[1][0].transaction_date).format(
                          "DD MMMM YYYY HH:mm:ss"
                        )}
                      </Col>
                    </Row>
                    <Row
                      style={{
                        fontSize: "12px",
                        marginBottom: "5px",
                        textAlign: "left"
                      }}
                    >
                      <Col span={4}>
                        <strong>
                          <IntlMessages id="transactionType"></IntlMessages>
                        </strong>
                      </Col>
                      <Col span={1}>:</Col>
                      <Col span={19}>{item[1][0].transaction_type_string}</Col>
                    </Row>
                    <Row
                      style={{
                        fontSize: "12px",
                        marginBottom: "5px",
                        textAlign: "left"
                      }}
                    >
                      <Col span={4}>
                        <strong>
                          <IntlMessages id="transactionNo"></IntlMessages>
                        </strong>
                      </Col>
                      <Col span={1}>:</Col>
                      <Col span={19}>{item[1][0].source_transaction_no}</Col>
                    </Row>
                    <div style={{ padding: "0 20px 20px" }}>
                      <PrintTable
                        columns={columns}
                        dataSource={item.dataSource}
                        pagination={false}
                      />
                    </div>
                  </div>
                );
              })
            ) : defaultExpandAllRows ? (
              <PrintTable
                columns={columns}
                dataSource={data}
                defaultExpandAllRows={true}
                expandedRowKeys={expandedRowKeys}
                expandedRowRender={expandedRowRender}
                pagination={false}
              />
            ) : (
              <PrintTable
                columns={columns}
                dataSource={data}
                pagination={false}
              />
            )}

            {/* footer */}
            <div className="pdf-footer">
              <div style={{ width: "100%" }}>
                <div className="line" />
              </div>
              <span>{siteConfig.footerReport}</span>
              <span>
                <IntlMessages
                  id="generatedOn"
                  values={{
                    dateTime: moment().format("HH:mm - DD MMMM YYYY")
                  }}
                >
                  {txt => `${txt}`}
                </IntlMessages>
              </span>
            </div>
            {/* end of: footer */}
          </PDFExport>
        </div>
        {this.props.children}
      </>
    );
  }
}

const mapStateToProps = state => {
  return { user: state.auth.user };
};

export default compose(connect(mapStateToProps))(PDFWrapper);
